.submit-button {
  margin: 10px;
  background: var(--vscode-button-background);
  color: var(--vscode-button-foreground);
  font-size: 1em;
  border: 0;
}

.button-box {
  width: 90px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px;
  height: 50px;
  border: 1px solid var(--vscode-checkbox-border);
  justify-content: center;
  text-align: center;
  border-radius: 5px;
  background: var(--vscode-editor-background);
  color: var(--vscode-editor-foreground);
  font-weight: 500;
  font-size: 0.8em;
  line-height: 1.9;
  cursor: pointer;
}
.button-box i {
  font-size: 30px;
  padding: 5px;
}
.button-box svg {
  width: 50px;
  padding: 5px;
}

.button-box.active {
  background: var(--vscode-button-background);
  color: var(--vscode-button-foreground);
}

.form-table {
  margin: 20px 10px;
  width: 100%;
}
.form-table td {
  padding-bottom: 5px;
  padding-right: 10px;
}
.form-table .td-left {
  width: 140px;
}
.form-table .ant-tag {
  background: var(--vscode-editor-background);
  color: var(--vscode-editor-foreground);
  border: 1px solid var(--vscode-checkbox-border);
  line-height: 27px;
  font-size: 1em;
}
.form-table .ant-tag i {
  color: var(--vscode-editor-foreground);
}
.form-table .ant-tag i:hover {
  color: var(--vscode-editor-foreground);
  opacity: 0.6;
}

.ant-select-selection {
  background: var(--vscode-editor-background);
  color: var(--vscode-editor-foreground);
  border: 1px solid var(--vscode-checkbox-border);
  font-size: 0.8em;
}
.ant-select-selection i {
  color: var(--vscode-editor-foreground);
}
